      marker: null,
      count: 1,
      map: null,
      isCTT: false,
      mapOptions: {
        lat: 37.55998,
        lng: 126.9858296,
        zoom: 15,
        zoomControl: true,
        zoomControlOptions: {position: 'TOP_RIGHT'},
        mapTypeControl: true,
      },
      initLayers: ['BACKGROUND', 'BACKGROUND_DETAIL', 'POI_KOREAN', 'TRANSIT', 'ENGLISH', 'CHINESE', 'JAPANESE']
    }),

    created() {
    	  axios.get('http://localhost:8080/yumyum/api/store/all')
            .then((response) => {
              response.data.forEach(element => {
                this.items.push(element);
                this.items.push({divider:true});
              });
            })
            .catch((error) => {
            	console.dir(error);
            });
      },

    methods: {
      onLoad(vue){
        this.map = vue;
      },
      onWindowLoad() {
      },
      onMarkerClicked(idx) {
        console.log(idx);
      },
      onMarkerLoaded(vue) {
        console.log(vue)
      },

      searchStore: function(){
        axios.get('http://localhost:8080/yumyum/api/store/find/'+this.storeName)
            .then((response) => {
              this.items = [];
              response.data.forEach(element => {
                this.items.push(element);
                this.items.push({divider:true});
              });
            })
            .catch((error) => {
            	console.dir(error);
            });
      },

      showDetail: function(id){
        console.log(id);
        
        axios.get('http://localhost:8080/yumyum/api/store/detail/'+id)
            .then((response) => {
              this.storeDetail = response.data;
            })
            .catch((error) => {
            	console.dir(error);
            });

        this.detail = true;
      },
  
    },

    mounted() {
      setInterval(() => this.count++, 1000);
    }
  }
